@import '../../themes/src/default.css';

:root {
    /* border */
    --tag-border-width: 1px;
    --tag-border-radius: var(--border-radius-pill);
}

.component {
    display: inline-flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    margin: 0;
    font-family: var(--font-family);
    text-decoration: none;
    border-width: var(--tag-border-width);
    border-style: solid;
    border-radius: var(--tag-border-radius);
    transition: background-color 0.2s ease, border 0.2s ease, color 0.2s ease;
    box-sizing: border-box;
    box-shadow: none;
    user-select: none;
    cursor: pointer;
    outline: none;

    &:disabled {
        cursor: var(--disabled-cursor);
    }
}

.focused {
    @mixin focus-outline;
}

.addons {
    display: flex;
    flex-shrink: 0;
    align-items: center;

    &:first-child {
        margin-right: var(--gap-xs);
    }

    &:last-child {
        margin-left: var(--gap-xs);
    }

    &:only-child {
        margin: 0;
    }
}

.xs {
    min-width: 60px;
    min-height: var(--size-xs-height);
    padding: var(--gap-2xs) var(--gap-m);
    font-size: 14px;
    line-height: 20px;
    font-weight: normal;

    @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
        /*   ,          ,   min-height
           -       ,   display: flex / inline-flex */
        height: 32px;
    }
}

.s {
    min-width: 80px;
    min-height: var(--size-s-height);
    padding: var(--gap-2xs) var(--gap-m);
    font-size: 16px;
    line-height: 20px;
    font-weight: normal;

    @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
        /*   ,          ,   min-height
           -       ,   display: flex / inline-flex */
        height: 48px;
    }
}

.m {
    @mixin action_component;
    min-width: 96px;
    min-height: var(--size-m-height);
    padding: var(--gap-2xs) var(--gap-xl);

    @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
        /*   ,          ,   min-height
           -       ,   display: flex / inline-flex */
        height: 56px;
    }
}

.l {
    @mixin action_primary_large;
    min-width: 120px;
    min-height: var(--size-l-height);
    padding: var(--gap-2xs) var(--gap-2xl);

    @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
        /*   ,          ,   min-height
           -       ,   display: flex / inline-flex */
        height: 64px;
    }
}

.xl {
    @mixin action_primary_large;
    min-width: 120px;
    min-height: var(--size-xl-height);
    padding: var(--gap-2xs) var(--gap-2xl);

    @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
        /*   ,          ,   min-height
           -       ,   display: flex / inline-flex */
        height: 72px;
    }
}
